.tile {
	position: absolute;
	width: 124px;
	height: 124px;
	-webkit-transition: '.25s all';
	-o-transition: '.25s all';
	transition: '.25s all';
	cursor: pointer;
	-webkit-transform: translateY(500px);
	    -ms-transform: translateY(500px);
	        transform: translateY(500px);
	overflow: hidden;
}

.tile:not(.empty):before {
	content:'';
	position: absolute;
	width: 160px;
	height: 160px;
	border: 8px solid rgb(241, 185, 0);
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	top: 50%;
	left: 50%;
	-webkit-transform: translateX(-50%) translateY(-50%);
	    -ms-transform: translateX(-50%) translateY(-50%);
	        transform: translateX(-50%) translateY(-50%);
	-webkit-transition: width 0.3s var(--easeOutExpo), height 0.3s var(--easeOutExpo);
	-o-transition: width 0.3s var(--easeOutExpo), height 0.3s var(--easeOutExpo);
	transition: width 0.3s var(--easeOutExpo), height 0.3s var(--easeOutExpo);
}

.tile:hover:before {
	width: 126px;
	height: 126px;
}

.tile.empty img {
	display: none;
}


