:root {
  --easeOutExpo: cubic-bezier(0.19, 1, 0.22, 1);
}

.puzzle-wrapper {
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translateX(-50%) translateY(-50%);
	    -ms-transform: translateX(-50%) translateY(-50%);
	        transform: translateX(-50%) translateY(-50%);
	width: 496px;
	height: 596px;
}

.puzzle {
	position: absolute;
	width: 50px;
	height: 50px;
	top: 50%;
	left: 50%;
	-webkit-transform: translateX(-50%) translateY(-50%);
	    -ms-transform: translateX(-50%) translateY(-50%);
	        transform: translateX(-50%) translateY(-50%);
	background: #fff;
	pointer-events: none;
	border: 10px solid #fff;
	-webkit-box-sizing: content-box;
	        box-sizing: content-box;
	overflow: hidden;
}

.puzzle .fullimage {
	opacity: 0;
	width: 100%;
	height: 100%;
}

.puzzle.intro {
	-webkit-transition: width 1s var(--easeOutExpo), height 1s var(--easeOutExpo);
	-o-transition: width 1s var(--easeOutExpo), height 1s var(--easeOutExpo);
	transition: width 1s var(--easeOutExpo), height 1s var(--easeOutExpo);
	width: 496px;
	height: 496px;
	pointer-events: auto;
}

.btn-solve {
	color: #fff;
	position: absolute;
	left: 50%;
	-webkit-transform: translateX(-50%);
	    -ms-transform: translateX(-50%);
	        transform: translateX(-50%);
	cursor: pointer;
	bottom: -10px;
	font-size: 14px;
	letter-spacing: 0.1em;
	font-family: 'Helvetica', 'Arial';
	-webkit-transition: opacity 0.5s var(--easeOutExpo);
	-o-transition: opacity 0.5s var(--easeOutExpo);
	transition: opacity 0.5s var(--easeOutExpo);
	opacity: 1;
}

.btn-solve.intro {
	opacity: 0;
}

.btn-solve:before, .btn-solve:after {
	content: '';
	position: absolute;
	top: 50%;
	left: -150%;
	width: 50px;
	height: 1px;
	-webkit-transform: translateX(-50%);
	    -ms-transform: translateX(-50%);
	        transform: translateX(-50%);
	background-color: #fff;
	-webkit-transition: left 0.3s var(--easeOutExpo);
	-o-transition: left 0.3s var(--easeOutExpo);
	transition: left 0.3s var(--easeOutExpo);
}


.btn-solve:after {
	left: 240%;
}

.btn-solve:hover:before, .btn-solve:hover:after, .btn-solve.intro:before, .btn-solve.intro:after {
	left: 50%;
}


